import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';

export const ResumeDownload = () => {
	const { formatMessage } = useIntlHelper();
	const data = useStaticQuery(QUERY);
	return <a href={`${data.resume.publicURL}`}>{formatMessage('general.resume')} </a>;
};

const QUERY = graphql`
	query ResumeQuery {
		resume: file(relativePath: { eq: "_data/assets/SoheilSadeghiCV.pdf" }) {
			publicURL
		}
	}
`;
