import * as React from 'react';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';

export const Contact = () => {
	const { formatMessage } = useIntlHelper();

	return (
		<form action="https://formspree.io/f/xxxxxxx" method="post">
			{/* 	<label htmlFor="email">{formatMessage('contact.email')}</label> <br /> */}
			<input name="Email" id="email" type="email" placeholder={formatMessage('contact.email')} /> <br />
			<textarea name="Message" placeholder={formatMessage('contact.text')}></textarea>
			<br />
			<button type="submit">{formatMessage('contact.button')}</button>
		</form>
	);
};
